@import url(https://fonts.googleapis.com/css2?family=Jost:wght@300&display=swap);


* {
    padding: 0;
    margin: 0;
    font-family: 'Jost', sans-serif;
}

body{
    background: #f3f2ef;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    height: 100vh;
    width: 100%;
    font-family: 'Jost', sans-serif;
}
.title{
    margin-bottom: 20px;
}
.box{
    background: #9EB6DA;
    padding: 15px;
    border-radius: 10px;
    height: 300px;
}
.inputData{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-bottom: 40px;
}
.inputData input{
    border-radius: 20px;
    padding: 10px;
    border: 2px solid #000;
    outline: none;
    font-size: 20px;
    font-weight: 600;
}
.errorMsg{
    display: flex;
    justify-content: center;
    font-size: 25px;
    font-weight: 600;
}
.info{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
}
.info h2{
    font-size: 30px;
    margin-bottom: 25px;
    text-transform: capitalize;
}
.info h1{
    margin-bottom: 20px;
}
.info h3{
    font-size: 15px;
}


body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

